#logo {
    position: relative;
    top: 25px;
    left: 45px;
    float: left;
    width: 125px;
    height: 50px;
    background-color: teal;
    opacity: .8;
}

#logo h1 {
        font-family: $title-font;
        color: aquamarine;
        font-size: 1.5em;
        text-align: center;
        text-transform: uppercase;
        padding-top: 12px;
    }

#logo a {
  text-decoration: none;
}

#game {
  position: relative;
  background-color: rgba(65,64,66, .8);
  width: 900px;
  height: 500px;
  margin-left: 45px;
  padding: 10px;
}

.next{
	color:white;
	background-color: $white;
}

.next:hover{
	background-color: $water-color;
}

